body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.chat-person-card {
    height: inherit !important;
    min-height: inherit !important;
}

.chat-person-card.mdl-card {

    height: 256px;
    background: #3E4EB8;
}

.chat-person-card > .mdl-card__actions {
    border-color: rgba(255, 255, 255, 0.2);
}

.chat-person-card > .mdl-card__title {
    -webkit-align-items: flex-start;
            align-items: flex-start;
}

.chat-person-card > .mdl-card__title > h4 {
    margin-top: 0;
}

.chat-person-card > .mdl-card__actions {
    display: -webkit-flex;
    display: flex;
    box-sizing: border-box;
    -webkit-align-items: center;
            align-items: center;
}

.chat-person-card > .mdl-card__actions > .material-icons {
    padding-right: 10px;
}

.chat-person-card > .mdl-card__title,
.chat-person-card > .mdl-card__actions,
.chat-person-card > .mdl-card__actions > .mdl-button {
    color: #fff;
}

.chat-person-card > .previous-chat-details {
    color: white;
}

.chat-person-card > .mdl-card__title {
    border-bottom: #3aa55f thin solid;
    font-size: 1.5em;
}
.mdl-layout__drawer .mdl-navigation .mdl-navigation__link {
    padding: 16px 20px;
}

.mdl-layout__drawer .mdl-navigation {
    padding-top: 0px;
}

.discussion {
    list-style: none;
    margin: 0;
    padding: 0 0 10px 0;
}

.discussion li {
    padding: 0.5rem;
    overflow: hidden;
    display: -webkit-flex;
    display: flex;
}

.discussion .avatar {
    width: 4px;
    position: relative;
}

.discussion .avatar img {
    display: block;
    width: 100%;
}

.other .avatar:after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 0;
    height: 0;
    border: 5px solid white;
    border-left-color: transparent;
    border-bottom-color: transparent;
}

.self {
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: flex-end;
            align-items: flex-end;
}

.self .messages {
    -webkit-order: 1;
            order: 1;
    border-bottom-right-radius: 0;
}

.self .avatar {
    -webkit-order: 2;
            order: 2;
}

.self .avatar:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 0;
    border: 5px solid white;
    border-right-color: transparent;
    border-top-color: transparent;
}

.messages {
    font-family: "Arial", "Helvetica", sans-serif;
    background: white;
    padding: 10px;
    border-radius: 2px;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}

.messages p {
    color: black;
    font-size: 1rem;
    margin: 0 0 0.2rem 0;
}

.messages span {
    font-size: 1rem;
    color: #ccc;
}

@-webkit-keyframes pulse {
    from {
        opacity: 0;
    }
    to {
        opacity: 0.5;
    }
}

@keyframes pulse {
    from {
        opacity: 0;
    }
    to {
        opacity: 0.5;
    }
}

.chat-box-message-form .mdl-textfield__input {
    border-bottom-color: white;
}


.mdl-dialog {
    border: none;
    box-shadow: 0 9px 46px 8px rgba(0, 0, 0, 0.14), 0 11px 15px -7px rgba(0, 0, 0, 0.12), 0 24px 38px 3px rgba(0, 0, 0, 0.2);
    width: 280px;
}

.mdl-dialog__title {
    padding: 24px 24px 0;
    margin: 0;
    font-size: 2.5rem;
}

.mdl-dialog__actions {
    padding: 8px 8px 8px 24px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row-reverse;
    flex-direction: row-reverse;
    -webkit-flex-wrap: wrap;
    flex-wrap: wrap;
}

.mdl-dialog__actions > * {
    margin-right: 8px;
    height: 36px;
}

.mdl-dialog__actions > *:first-child {
    margin-right: 0;
}

.mdl-dialog__actions--full-width {
    padding: 0 0 8px 0;
}

.mdl-dialog__actions--full-width > * {
    height: 48px;
    -webkit-flex: 0 0 100%;
    flex: 0 0 100%;
    padding-right: 16px;
    margin-right: 0;
    text-align: right;
}

.mdl-dialog__content {
    padding: 20px 24px 24px 24px;
    color: rgba(0, 0, 0, 0.54);
}


.isa_info, .isa_success, .isa_warning, .isa_error {
    margin: 10px 0px;
    padding: 12px;
    border: black thin solid;
    border-radius: 5px;

}

.isa_info {
    color: #00529B;
    background-color: #BDE5F8;

}

.isa_success {
    color: #4F8A10;
    background-color: #DFF2BF;
}

.isa_warning {
    color: #9F6000;
    background-color: #FEEFB3;
}

.isa_error {
    color: #D8000C;
    background-color: #FFD2D2;
}

.isa_info i, .isa_success i, .isa_warning i, .isa_error i {
    margin: 10px 22px;
    font-size: 2em;
    vertical-align: middle;
}
